<template>
    <div class="container">
        <a href="http://localhost:8001/admin/index.html" style="text-align:center;"><h3>üëâÂêéÂè∞ÁÆ°ÁêÜÂÖ•Âè£üëà</h3></a>
        <!-- ÊêúÁ¥¢Ê°Ü -->
        <form class="city_form" v-on:submit.prevent>
            <p class="tip">ÂΩìÂâçÂÆö‰ΩçÂüéÂ∏ÇÔºö<span>{{cityname}}</span></p>
            <p class="tip">ËØ∑Âú®‰∏ãÊñπÊêúÁ¥¢Âπ∂ÈÄâÊã©ÊÇ®ÂΩìÂâçÁöÑËØ¶ÁªÜ‰ΩçÁΩÆüëáüëáüëá</p>
            <el-autocomplete class="search" popper-class="my-autocomplete" :popper-append-to-body="false" v-model="inputVaule" :fetch-suggestions="querySearch" placeholder="ËæìÂÖ•Â≠¶Ê†°„ÄÅÂïÜÂä°Ê•º„ÄÅÂú∞ÂùÄ" @select="handleSelect" >
                <i class="el-icon-delete el-input__icon" slot="suffix" @click="handleIconClick"></i>
                <template slot-scope="{ item }">
                    <p>ÂéÜÂè≤ËÆ∞ÂΩï</p>
                    <div class="name">{{ item.value }}</div>
                    <span class="addr">{{ item.address }}</span>
                </template>
                <el-button slot="append" icon="el-icon-search" @click="postpois"></el-button>
            </el-autocomplete>
        </form>
        <!-- ÊêúÁ¥¢ÁªìÊûú -->
        <ul class="getpois_ul">
            <li v-for="(item, index) in placelist" @click='nextpage(index,item.geohash)' :key="index">
                <h4 class="pois_name">{{item.name}}</h4>
                <p class="pois_address">{{item.address}}</p>
            </li>
        </ul>
        <div v-if="placeNone" class="search_none_place" >ÂæàÊä±Ê≠âÔºÅÊó†ÊêúÁ¥¢ÁªìÊûú</div>
    </div>
</template>

<script>
import { cityGuess , searchplace } from 'src/api/getData'
import { getStore, setStore, removeStore } from 'src/config/mUtils'

export default {
    data(){
        return{
            inputVaule:'', // ÊêúÁ¥¢Âú∞ÂùÄ
            cityid:'', // ÂΩìÂâçÂüéÂ∏Çid
            cityname:'', // ÂΩìÂâçÂüéÂ∏ÇÂêçÂ≠ó
            placelist:[], // ÊêúÁ¥¢ÂüéÂ∏ÇÂàóË°®
            placeHistory:[], // ÂéÜÂè≤ÊêúÁ¥¢ËÆ∞ÂΩï

            historylist:[], // ÂéÜÂè≤ÊêúÁ¥¢ËÆ∞ÂΩï
            placeNone: false, // ÊêúÁ¥¢Êó†ÁªìÊûúÔºåÊòæÁ§∫ÊèêÁ§∫‰ø°ÊÅØ
        }
    },
	mounted(){
        // Ëé∑ÂèñÂΩìÂâçÂüéÂ∏Ç
        cityGuess().then(res => {
            this.cityname = res.data.name;
            this.cityid = res.data.id;
        });
        this.initData();
    },
    methods:{
        //ÂàùÂßãÂåñÊï∞ÊçÆ
        initData(){
            //Ëé∑ÂèñÊêúÁ¥¢ÂéÜÂè≤ËÆ∞ÂΩï
            this.historylist = JSON.parse(getStore('placeHistory'));
        },
        //ÂèëÈÄÅÊêúÁ¥¢‰ø°ÊÅØinputVaule
        postpois(){
            //ËæìÂÖ•ÂÄº‰∏ç‰∏∫Á©∫Êó∂ÊâçÂèëÈÄÅ‰ø°ÊÅØ
            if (this.inputVaule) {
                searchplace(this.cityid, this.inputVaule).then(res => {
                    this.placelist = res.data;
                    this.placeNone = res.data.length? false : true;
                })
            }
        },
        /**
         * ÁÇπÂáªÊêúÁ¥¢ÁªìÊûúËøõÂÖ•‰∏ã‰∏ÄÈ°µÈù¢Êó∂ËøõË°åÂà§Êñ≠ÊòØÂê¶Â∑≤ÁªèÊúâ‰∏ÄÊ†∑ÁöÑÂéÜÂè≤ËÆ∞ÂΩï
         * Â¶ÇÊûúÊ≤°ÊúâÂàôÊñ∞Â¢ûÔºåÂ¶ÇÊûúÊúâÂàô‰∏çÂÅöÈáçÂ§çÂÇ®Â≠òÔºåÂà§Êñ≠ÂÆåÊàêÂêéËøõÂÖ•‰∏ã‰∏ÄÈ°µ
         */
        nextpage(index, geohash){
            let history = getStore('placeHistory');
            let choosePlace = this.placelist[index];
            if (history) {
                let checkrepeat = false;
                this.placeHistory = JSON.parse(history);
                this.placeHistory.forEach(item => {
                    if (item.geohash == geohash) {
                        checkrepeat = true;
                    }
                })
                if (!checkrepeat) {
                    this.placeHistory.push(choosePlace)
                }
            }else {
                this.placeHistory.push(choosePlace)
            };
            setStore('placeHistory',this.placeHistory);
            this.$router.push({path:'/msite', query:{geohash}});
        },
        clearAll(){
            removeStore('placeHistory');
            this.initData();
        },


        // ÁÇπÂáªËæìÂÖ•Ê°ÜÊó∂ÂàóÂá∫ÂéÜÂè≤ËÆ∞ÂΩï,queryString‰∏∫ËæìÂÖ•Â≠óÁ¨¶‰∏≤ÔºåcbÊòØ Ë∞ÉÁî® callback ËøîÂõûÂª∫ËÆÆÂàóË°®ÁöÑÊï∞ÊçÆ
        querySearch(queryString, cb) {
            var historylist = this.historylist ? this.historylist.map(object=>{return {
                value:object.name,
                address:object.address,
                geohash:object.geohash,
                latitude:object.latitude,
                longitude:object.longitude
            }}) : [];
            //Â¶ÇÊûúËæìÂÖ•Â≠óÁ¨¶‰∏≤‰∏∫ÈùûÁ©∫ÔºåÂàôÊòæÁ§∫Áõ∏ÂÖ≥ÂéÜÂè≤ËÆ∞ÂΩïÔºåÂ¶ÇÊûú‰∏∫Á©∫Â∞±ÊòæÁ§∫ÂÖ®ÈÉ®ÂéÜÂè≤ËÆ∞ÂΩï
            var results = queryString ? historylist.filter(this.createFilter(queryString)) : historylist;
            cb(results);
        },
        //ËøáÊª§ËæìÂÖ•Â≠óÁ¨¶‰∏≤
        createFilter(queryString) {
            return (restaurant) => {
            return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
            };
        },
        //ÈÄâ‰∏≠ÂêéÂ§ÑÁêÜ
        handleSelect(item) {
            this.$router.push({path:'/msite', query:{geohash:item.geohash}})
        },
        handleIconClick(ev) {
            this.clearAll();
            console.log(ev)
        }
    },
}

</script>

<style lang="scss" scoped>
    @import '../../style/mixin';
    .home {
        width: 50%;
        text-align: center;
        margin-left: -0.5rem;
    }
    .container {
        margin: 0 auto;
        margin-top: 24px;
        width: 600px;
        .city_form{
            .tip {
                span {
                    color: #409EFF;
                }
            }
            .search{
                width: 100%;
            }
            .my-autocomplete {
                li {
                    line-height: normal;
                    padding: 7px;
                    .name {
                    text-overflow: ellipsis;
                    overflow: hidden;
                    }
                    .addr {
                    font-size: 12px;
                    color: #b4b4b4;
                    }
                    .highlighted .addr {
                    color: #ddd;
                    }
                }
            }
        }
        .getpois_ul{
            li{
                padding-top: 21px;
                border-bottom: 1px solid $bc;
                .pois_name{
                    margin: 0 auto 0.35rem;
                    width: 90%;
                    @include sc(0.65rem, #333);
                }
                .pois_address{
                    width: 90%;
                    margin: 0 auto 0.55rem;
                    @include sc(0.45rem, #999);
                }
            }
        }
        .search_none_place{
            text-align: center;
            margin: 21px auto;
        }
}
</style>
